:root {
  /* Base color */
  --white: hsl(0, 0%, 98%);
  --black: hsl(0, 0%, 10%);
  /* Website color palette */
  --primary: hsl(0, 2%, 90%);
  --secondary_light: hsl(214, 18%, 35%);
  --secondary: hsl(214, 18%, 25%);
  --accent: hsl(318, 49%, 50%);
  /* Shadow */
  --shadow: transparent;
  /* Font styles */
  --ubuntu: "Ubuntu", sans-serif;
  --roboto: "Roboto Mono", monospace;
}

.hidden {
  opacity: 0;
  transform: translateX(-100%);
  filter: blur(2em);
  transition: 800ms opacity ease-in-out 200ms, 800ms transform ease-in-out 200ms;
}

.hidden-project {
  opacity: 0;
  transform: translateY(100%);
  filter: blur(2em);
  transition: 800ms opacity ease-in-out 200ms, 800ms transform ease-in-out 200ms;
}

.show {
  opacity: 1;
  filter: blur(0);
  transform: translateX(0) translateY(0);
}

.idle {
  animation-name: idle_anim;
  animation-duration: 3s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  animation-fill-mode: both;
}

.textbox_anim {
  animation-name: show_box;
  animation-duration: 400ms;
  animation-timing-function: ease-in-out;
  animation-fill-mode: forwards;
}

.text_anim {
  animation-name: show_text;
  animation-duration: 800ms;
  animation-timing-function: ease-out;
  animation-fill-mode: forwards;
  animation-delay: 400ms;
}

.scroll_anim {
  animation-name: show_scroll;
  animation-duration: 300ms;
  animation-timing-function: ease-in-out;
  animation-fill-mode: forwards;
  animation-delay: 1s;
}

/* KEYFRAMES */
@keyframes show_box {
  to {
    transform: scaleY(100%);
  }
}
@keyframes show_text {
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes show_scroll {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes idle_anim {
  5% {
    transform: translateY(0.25em);
  }
  10% {
    transform: translateY(0);
  }
  15% {
    transform: translateY(0.25em);
  }
  20% {
    transform: translateY(-0.05em);
  }
  30% {
    transform: translateY(0.05em);
  }
  40% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(0);
  }
  60% {
    transform: translateY(0);
  }
  70% {
    transform: translateY(0);
  }
  80% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(0);
  }
}
@media (prefers-reduced-motion) {
  .hidden {
    transition: none;
  }
}
/*
  Josh's Custom CSS Reset
  https://www.joshwcomeau.com/css/custom-css-reset/
*/
*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

html,
body {
  min-height: 100%;
  font-size: 100%;
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

input,
button,
textarea,
select {
  font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

#root,
#__next {
  isolation: isolate;
}

body {
  font-family: var(--roboto);
  height: 100svh;
  background-color: var(--primary);
  font-size: 1em;
}

.header {
  height: 95svh;
  width: 100%;
  margin-top: 5vh;
  display: grid;
  place-content: center;
  grid-template-rows: 45% 40% 15%;
}
.header__title {
  place-self: center;
  height: fit-content;
  width: 16ch;
  font-size: clamp(2rem, 1.5rem + 2vw, 5rem);
  letter-spacing: 0.2ch;
  color: var(--secondary);
  text-align: center;
  text-shadow: 0.1em 0.1em var(--shadow);
}
.header__textbox {
  place-self: start;
  width: 80%;
  margin-inline: auto;
  padding-inline: 0.5em;
  padding-block: 1.5em;
  border-left: 0.25em solid var(--secondary);
  border-right: 0.25em solid var(--secondary);
  transform: scaleY(0);
  overflow-x: hidden;
}
@media screen and (min-width: 48em) {
  .header__textbox {
    width: 100%;
    padding-inline: 1em;
    padding-block: 2em;
    border-left: 0.5em solid var(--secondary);
    border-right: 0.5em solid var(--secondary);
  }
}
.header__text {
  font-size: clamp(0.75rem, 0.75rem + 0.5vw, 1.25rem);
  color: var(--secondary);
  text-shadow: 0.1em 0.1em var(--shadow);
  transform: translateX(-100%);
  opacity: 0;
}
.header__scroll {
  cursor: pointer;
  text-decoration: none;
  text-transform: uppercase;
  place-self: center;
  color: var(--secondary);
  font-size: 0.8em;
  padding: 1em;
  transform: translateY(100%);
  opacity: 0;
  transition: 250ms background-color ease-in-out, 250ms color ease-in-out;
}
@media screen and (min-width: 48em) {
  .header__scroll {
    font-size: 1em;
  }
}
.header__scroll > svg {
  width: 1em;
  height: 0.8em;
  padding-top: 0.25em;
  margin-inline: auto;
  fill: var(--secondary);
}
@media screen and (min-width: 48em) {
  .header__scroll > svg {
    width: 1.5em;
    height: 1em;
  }
}
.header__scroll:hover {
  background-color: var(--secondary);
  color: var(--primary);
}
.header__scroll:is(:hover) > svg {
  fill: var(--primary);
}

.footer {
  width: 100%;
  padding: 15%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: var(--secondary);
  color: var(--primary);
  font-size: 0.8em;
  position: relative;
  z-index: 999;
  gap: 1em;
}
@media screen and (min-width: 26.5em) {
  .footer {
    flex-direction: row;
    padding-block: 5vh;
    gap: 0;
    align-content: center;
    justify-content: space-evenly;
  }
}
@media screen and (min-width: 48em) {
  .footer {
    font-size: 1em;
    padding-block: 10vh;
  }
}
@media screen and (min-width: 48em) {
  .footer {
    font-size: 1.25em;
  }
}
.footer__info {
  height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.footer__links {
  height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.footer__links h3 {
  white-space: nowrap;
}
.footer a {
  text-decoration: none;
  color: var(--primary);
  padding-block: 0.25em;
}
.footer a:hover {
  color: var(--accent);
}

.projects_detail {
  width: 100%;
  height: fit-content;
  background-color: var(--secondary_light);
  padding-block: calc(1.5em + 5vw);
  display: flex;
  flex-direction: column;
}

.projects_title {
  width: fit-content;
  margin-inline: auto;
  font-size: clamp(1.25rem, 1.25rem + 2vw, 3rem);
  color: var(--primary);
}

.projects_descr {
  width: 85%;
  padding-top: 1em;
  margin-inline: auto;
  font-size: clamp(0.75rem, 0.75rem + 0.5vw, 1.25rem);
  color: var(--primary);
}
@media screen and (min-width: 26.5em) {
  .projects_descr {
    width: 75%;
  }
}
@media screen and (min-width: 64em) {
  .projects_descr {
    width: 65ch;
  }
}

.projects_container {
  display: grid;
  width: 90%;
  min-height: 90svh;
  margin-inline: auto;
  position: relative;
  place-content: center;
  grid-template-columns: repeat(auto-fill, 20em);
  grid-gap: calc(2em + 2vw);
  margin-block: 5em 10em;
}
@media screen and (min-width: 48em) {
  .projects_container {
    grid-template-columns: repeat(auto-fill, 30em);
  }
}

.project {
  width: 100%;
  height: fit-content;
  position: relative;
  display: flex;
  flex-direction: column;
  box-shadow: 0.5em 0.5em 1em hsla(0, 0%, 50%, 0.5);
  transition: 200ms box-shadow ease-in-out, 500ms transform ease-in-out, 500ms opacity ease-in-out;
}
.project:hover {
  box-shadow: 0.25em 1em 1em hsla(0, 0%, 50%, 0.5);
}
.project__image {
  width: 100%;
  height: 12.5em;
  object-fit: cover;
  object-position: 50% 50%;
  filter: grayscale(0.1);
}
@media screen and (min-width: 48em) {
  .project__image {
    height: 17.5em;
  }
}
.project__card {
  width: 100%;
  display: flex;
  padding: 0.75em;
  flex-direction: row;
  background-color: var(--white);
  gap: 2em;
}
.project__detail {
  width: 80%;
  height: 100%;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  text-transform: uppercase;
  color: var(--black);
}
.project__detail p {
  font-size: 1.25em;
  font-weight: 800;
}
.project__detail div {
  height: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, 5ch);
  grid-template-rows: repeat(2, 1fr);
  grid-auto-flow: column dense;
}
.project__detail div > :first-child {
  grid-column-start: 1;
  grid-row-start: 1;
}
.project__detail div p {
  font-size: 1rem;
  font-weight: 600;
}
.project__buttons {
  grid-column-start: 4;
  width: fit-content;
  background-color: transparent;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 0.25em;
}
.project__buttons button {
  width: fit-content;
  border: none;
  white-space: nowrap;
  font-size: 1rem;
  background-color: var(--black);
  color: var(--primary);
  padding: 0.5em 0.75em;
  transition: 300ms background-color ease-in-out;
  cursor: pointer;
}
.project__buttons button:hover {
  background-color: var(--accent);
  color: var(--primary);
}

.nav {
  height: fit-content;
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  z-index: 999;
  transition: 300ms margin ease-in-out, 300ms padding ease-in-out;
  justify-content: flex-end;
}
.nav--horizontal {
  background-color: var(--secondary);
  width: 100%;
  display: flex;
  flex-direction: row;
  margin-top: 0;
  font-size: clamp(0.65rem, 0.5rem + 0.5vw, 1rem);
}
@media screen and (min-width: 48em) {
  .nav--horizontal {
    font-size: clamp(0.7rem, 0.5rem + 0.5vw, 1rem);
    padding-right: calc(1em + 5vw);
  }
}
.nav--vertical {
  width: fit-content;
  margin-top: 2vh;
  padding-right: 0;
  font-size: clamp(0.7rem, 0.5rem + 0.5vw, 0.9rem);
  position: fixed;
  flex-direction: column;
  background-color: var(--primary);
  border-bottom: 0.15em solid var(--secondary);
  border-top: 0.15em solid var(--secondary);
  border-left: 0.15em solid var(--secondary);
  box-shadow: 0.25em 0.35em 0 var(--black);
}
.nav--mobile {
  height: fit-content;
  position: fixed;
  box-sizing: content-box;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.nav--mobile > .nav__item:nth-child(4) {
  order: 1;
}
.nav--mobile > .nav__item:nth-child(1) {
  order: 2;
}
.nav--mobile > .nav__item:nth-child(2) {
  order: 2;
}
.nav--mobile > .nav__item:nth-child(3) {
  order: 2;
}
.nav--mobile > .nav__item a {
  visibility: visible;
  transform: translateX(100%);
  font-size: clamp(0.7rem, 0.7rem + 0.5vw, 1rem);
  background-color: var(--black);
  color: var(--primary);
  transition: 200ms transform ease-in-out, 300ms background-color ease-in-out, 200ms visibility ease-in-out;
}
.nav--mobile > .nav__item svg {
  width: 2.5em;
  height: 2.5em;
  padding: 0.5em;
  background-color: var(--black);
  box-sizing: content-box;
}
.nav__item {
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.nav__item > a {
  width: 100%;
  display: flex;
  position: relative;
  z-index: 1;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  color: var(--primary);
  visibility: hidden;
  padding: 1.5em;
  transform: translateX(15em);
  transition: 300ms background-color ease-in-out, 300ms color ease-in-out, 200ms visibility ease-in-out, 400ms transform ease-in-out;
}
@media screen and (min-width: 48em) {
  .nav__item > a {
    visibility: visible;
    transform: translateX(0);
  }
}
.nav__item:has(:hover) svg {
  fill: var(--accent);
  cursor: pointer;
}
.nav__item > svg {
  width: 100%;
  height: 100%;
  display: block;
  position: relative;
  z-index: 999;
  padding-inline: 1em;
  background-color: var(--secondary);
  transition: 300ms background-color ease-in-out;
}
.nav__item > svg:hover {
  background-color: var(--accent);
}
@media screen and (min-width: 48em) {
  .nav__item > svg {
    display: none;
  }
}
.nav__item > svg.hide_icon {
  display: none;
}
.nav__item > .to-left {
  text-align: end !important;
  color: var(--secondary);
}
.nav__item > a:hover {
  background-color: var(--accent);
  color: var(--primary);
}